___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin-left: 10%;
  margin-top: 1em;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
}

h1 {
  font-size: 5em;
}

h2 {
  font-size: 2.5em;
}

main {
  display: flex;
  gap: 10%;
  margin: 2% 5% 10% 5%;
}

section {
  min-width: 40%;
}

#scores-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1em;
}

button {
  background: rgba(255, 255, 255, 0.2);
  border: 3px solid;
  box-shadow: 3px 2px 2px;
  padding: 8px 16px;
  font-size: 18px;
  font-weight: 700;
  width: 6em;
  transition: transfom 0.2s;
}

button:hover {
  transform: scale(1.1);
}

button:focus {
  transform: scale(1);
}

ul {
  border: 3px solid;
  margin-top: 5%;
  list-style: none;
}

form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 2em;
  margin-top: 5%;
  width: 75%;
}

form input {
  width: 100%;
  border: 3px solid;
}

form input,
.list {
  font-size: 1.2em;
  font-weight: 600;
  height: 2em;
  padding: 0.5em;
  background: rgba(255, 255, 255, 0.5);
}

.list:nth-child(even) {
  background: rgba(230, 224, 224, 0.8);
}

footer {
  background: rgb(230, 224, 224);
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 1% 10%;
  font-size: 2em;
  font-weight: 700;
}

@media (width <= 592px) {
  body {
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-size: cover;
  }

  h1 {
    font-size: 4em;
  }

  main {
    flex-direction: column;
    align-items: center;
    gap: 0px;
    margin: 5% 1% 20% 1%;
  }

  section {
    width: auto;
  }

  ul {
    margin-bottom: 5%;
  }

  form {
    width: auto;
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,mDAA8B;EAC9B,sBAAsB;AACxB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,QAAQ;EACR,oBAAoB;AACtB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,QAAQ;AACV;;AAEA;EACE,oCAAoC;EACpC,iBAAiB;EACjB,uBAAuB;EACvB,iBAAiB;EACjB,eAAe;EACf,gBAAgB;EAChB,UAAU;EACV,yBAAyB;AAC3B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,qBAAqB;EACrB,QAAQ;EACR,cAAc;EACd,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,iBAAiB;AACnB;;AAEA;;EAEE,gBAAgB;EAChB,gBAAgB;EAChB,WAAW;EACX,cAAc;EACd,oCAAoC;AACtC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,8BAA8B;EAC9B,eAAe;EACf,SAAS;EACT,QAAQ;EACR,OAAO;EACP,eAAe;EACf,cAAc;EACd,gBAAgB;AAClB;;AAEA;EACE;IACE,mDAA8B;IAC9B,sBAAsB;EACxB;;EAEA;IACE,cAAc;EAChB;;EAEA;IACE,sBAAsB;IACtB,mBAAmB;IACnB,QAAQ;IACR,oBAAoB;EACtB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,WAAW;EACb;AACF","sourcesContent":["* {\r\n  box-sizing: border-box;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\nbody {\r\n  margin-left: 10%;\r\n  margin-top: 1em;\r\n  background: url('./ac7-1.png');\r\n  background-size: cover;\r\n}\r\n\r\nh1 {\r\n  font-size: 5em;\r\n}\r\n\r\nh2 {\r\n  font-size: 2.5em;\r\n}\r\n\r\nmain {\r\n  display: flex;\r\n  gap: 10%;\r\n  margin: 2% 5% 10% 5%;\r\n}\r\n\r\nsection {\r\n  min-width: 40%;\r\n}\r\n\r\n#scores-heading {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  gap: 1em;\r\n}\r\n\r\nbutton {\r\n  background: rgba(255, 255, 255, 0.2);\r\n  border: 3px solid;\r\n  box-shadow: 3px 2px 2px;\r\n  padding: 8px 16px;\r\n  font-size: 18px;\r\n  font-weight: 700;\r\n  width: 6em;\r\n  transition: transfom 0.2s;\r\n}\r\n\r\nbutton:hover {\r\n  transform: scale(1.1);\r\n}\r\n\r\nbutton:focus {\r\n  transform: scale(1);\r\n}\r\n\r\nul {\r\n  border: 3px solid;\r\n  margin-